    <div v-else-if="lang===`it`">
      <h4 class="fw-bold"> L'unica brigata del genere</h4>

      <p class="fw-bold">
        Dopo la sconfitta della Francia alla fine di giugno 1940, gli inglesi si aspettavano che migliaia di soldati francesi e un pugno di polacchi si sarebbero trasferiti dalla Siria alla Palestina sotto il loro controllo. Qual è stato il loro stupore quando hanno visto che era esattamente l'opposto. Qui, in pieno ordine e con le armi, la Brigata Fucilieri dei Carpazi venne in loro aiuto. L'inizio dell'esistenza della Brigata Fucilieri dei Carpazi riceve l'ordine di formarla, annunciato dal Comandante in Capo, Generale Władysław Sikorski il 12 aprile 1940. Il fatto è, tuttavia, che la creazione delle truppe polacche in Medio Oriente con l'esercito locale francese è stato pensato dallo stato maggiore del Comandante in Capo dall'inizio di quest'anno. A gennaio, l'addetto militare in Romania e Ungheria ricevette l'ordine di sviluppare un piano per l'evacuazione delle truppe polacche nel Levante (il piano era ufficialmente chiamato Evacuazione B), come allora venivano chiamate le coste africane del Mediterraneo. Appena due giorni dopo l'ordine sull'organizzazione della brigata polacca presso l'Esercito del Levante sotto il generale Weygand, il 14 aprile, lo staff organizzativo e istruttore è partito per Beirut - un totale di 32 ufficiali e 20 privati. Alla fine di quel mese, il colonnello certificato Stanisław Kopański, nominato comandante della brigata, arrivò nella capitale del Libano. Secondo i posti di lavoro a tempo pieno, la Brigata Fucilieri dei Carpazi doveva avere quasi settemila soldati e doveva essere un'unità di montagna. L'aggettivo "Karpacka" nel nome avrebbe dovuto descrivere la direzione da cui sarebbe venuto per liberare la patria, ma allo stesso tempo rifletteva bene le esperienze di molti dei suoi soldati che dovettero sfondare i Carpazi per raggiungere esso.


      </p>
      <h4 class="fw-bold"> 

Brigata intelligente
</h4>
      <p>
        Ben presto, i volontari iniziarono a fare rapporto in un campo militare vicino alla città siriana di Homs. La maggior parte di loro erano persone non solo indurite dalle drammatiche esperienze degli ultimi mesi, ma anche istruite. La brigata aveva la più alta percentuale di soldati con un'istruzione superiore nell'intero esercito polacco e con esperienza militare. Come è stato sottolineato nella storia ufficiale dell'unità: "Nelle file della brigata non c'erano solo soldati di settembre, ma anche un certo numero di coloro che avevano alle spalle le campagne francese e norvegese, e persino combattimenti esotici in Africa orientale e l'Estremo Oriente nelle unità della Legione Straniera ". Tali soldati volevano combattere i tedeschi il prima possibile, ma il loro alleato francese li ha completamente delusi a questo riguardo. Quando la notizia della resa della Francia raggiunse la Siria il 19 giugno 1940, il comando dell'esercito del Levante si schierò con il maresciallo Pétain, che acconsentì a collaborare con la Germania. Di fronte alla minaccia del disarmo e dell'internamento della brigata polacca, il generale di brigata Stanisław Kopański (promosso a questo livello il 3 maggio) ha deciso di agire con decisione e di arrivare a tutti i costi nella Palestina controllata dagli inglesi. Fortunatamente, il capo di stato maggiore dell'esercito del Levante, il colonnello de Larminat, si è dimostrato fedele ai suoi alleati e, sotto la propria responsabilità, ha dotato la brigata di armi, munizioni e mezzi di trasporto, tra cui più di 100 camion. Così equipaggiata, la brigata polacca fece scalpore tra i soldati britannici che la accolsero in Palestina.
      </p>

      <h4 class="fw-bold">I topi di Tobruk</h4>
      <p>
        I soldati polacchi furono inviati al campo di Latrun, dove furono equipaggiati con attrezzature e armi britanniche. Rispetto alle carenze e al disordine francesi, il quartiermastro britannico era un bel contrasto per i polacchi. Nell'ottobre 1940, parte della brigata fu trasportata ad Alessandria d'Egitto, dove fu utilizzata per costruire fortificazioni e proteggere i campi di prigionieri di guerra con gli italiani catturati. Gli inglesi hanno avuto un problema con l'invio della brigata al fronte in un primo momento, poiché la Polonia non era in guerra con l'Italia, ma quando i carri armati Afrika Korps del generale Erwin Rommel, il futuro "Desert Fox", apparvero nel deserto, questi dubbi persero la loro rilevanza. Il 12 gennaio 1941 fu completata la riorganizzazione della brigata secondo gli schemi britannici e da quel momento fu chiamata la Brigata Indipendente dei Fucilieri dei Carpazi. In vista dell'invasione tedesca della Grecia nella primavera del 1941, si decise di trasferirla sul fronte greco. È iniziato l'addestramento in montagna e il riarmo delle truppe di montagna, ma prima che fosse completato, la Grecia cadde. Allo stesso tempo, sul fronte africano, il generale Rommel lanciò la sua offensiva, quindi la brigata fu rapidamente trasferita alla fortezza di Marsa Matruh, e poi alla fortificata Sidi Baggush. Qui, i polacchi incontrarono soldati esotici come sudafricani, australiani, indiani, neozelandesi, soldati della Legione straniera francese libera - tutte le nazioni dell'Impero britannico e dei suoi alleati in Africa. È stata una grande competizione per guadagnare fama militare, soprattutto perché i soldati sudafricani e australiani, per non parlare della Legione Straniera, avevano molta più esperienza nei combattimenti nel deserto. Eppure, quando i Carpazi si unirono all'equipaggio di Tobruk alla fine di agosto 1941, dimostrarono di essere un nemico eccezionalmente duro e combattivo per i tedeschi e gli italiani che assediavano questa fortezza chiave in Libia. All'inizio di ottobre, la brigata aveva la sezione di difesa più pesante: la cosiddetta breccia nell'area della collina di Ras el Medauar. Il nemico aveva ottime posizioni di tiro sulla collina e la distanza tra i combattenti era in media di 800, e qua e là 300-200 metri! Questo è ciò che scrive al riguardo il generale Kopański nelle sue memorie: "... il comandante della fortezza mi ha chiesto per quanto tempo i soldati della brigata dei Carpazi potrebbero rimanere nella breccia, perché vista la probabilità di un'offensiva dell'8a armata all'inizio di novembre, sarà difficile per lui riorganizzarsi [...]. Risposi che il record australiano di rimanere nella breccia era di quattro settimane; che i polacchi potevano resistere una settimana in più [...]. A quel tempo, non potevo nemmeno avere immaginavo che, dopo aver posticipato l'inizio dell'offensiva, e poi la sua lenta avanzata, i soldati dei Carpazi avrebbero stabilito il record di dieci settimane di permanenza nella breccia! Nessuno dei due dimenticherà Medauar per il resto della loro vita ”.</p>

      <h4 class="fw-bold">Elite del 2 ° Corpo</h4>
      <p>
        
A Tobruk, i Carpazi, con le loro pattuglie da combattimento e l'immaginazione degli ulani (che non è solo una metafora, perché i soldati del reggimento dei Lancieri dei Carpazi vi si distinguevano particolarmente) meritavano pienamente l'onorevole titolo di Tobruk Rats, ma dopo che la fortezza fu sbloccata nel dicembre 1941 dal summenzionato attacco britannico 8 L'esercito dovette affrontare nuovi compiti. Gran parte della brigata polacca (senza i lancieri dei Carpazi) entrò a far parte del 13 ° Corpo dell'8a Armata e si distinse nuovamente, rompendo le linee di difesa nemiche a Gazala. Grazie a ciò, l'8a armata fu in grado di lanciare un'ampia offensiva e spostare i tedeschi dalla Cirenaica. Dal nuovo 1942, singole unità della brigata dei Carpazi sostenevano gli inglesi nei loro duri combattimenti con l'implacabile Rommel. L'artiglieria della brigata schiacciò le fortificazioni della fortezza di Bardia. I suoi camminatori insieme ai francesi liberi difesero il forte di El Mekili, e in seguito si ritrovarono di nuovo a Gazala e occuparono coraggiosamente posizioni a Carmuset et Regem. Finalmente è il momento di riposarsi e riorganizzarsi. Nel marzo 1942, la brigata fu ritirata dalla linea e diretta prima al campo di El Amiriya e poi in Palestina. Era anche collegato al fatto che nel frattempo era già in corso l'evacuazione dell'esercito del generale Anders dall'Unione Sovietica. Nel maggio 1942, la Brigata indipendente dei fucilieri dei Carpazi fu sciolta e il suo quadro divenne la spina dorsale della 3a divisione di fucili dei Carpazi dell'esercito polacco a est, e poi del 2 ° corpo polacco. I soldati che avevano combattuto nel deserto alle loro spalle furono chiamati Ramses dai compagni d'armi che provenivano dai sovietici. Inizialmente, c'erano alcuni antagonismi tra Ramses e gli ortodossi (o buzułukami, come venivano chiamati i soldati dell'esercito polacco in URSS). I Ramses furono accusati di esaltarsi e di glorificarsi con la gloria di guerra che avevano vinto a Tobruk ea Gazala. Ma tutto finì quando la 3a Divisione Fucilieri dei Carpazi era in Italia alla fine del 1943. Sul fronte italiano, i Carpazi avevano più che sufficienti opportunità per confermare il loro valore di combattimento e vincere più allori in combattimento.





      </p>

